@keyframes menuAnimationTopSpan {
  25% {
    top: 50%;
  }
  50% {
    transform: rotate(-45deg);
    top: 50%;
  }
  100% {
    transform: rotate(-45deg);
    top: 50%;
  }
}
@keyframes menuAnimationBottomSpan {
  25% {
    width: 100%;
    bottom: 50%;
  }
  50% {
    transform: rotate(-45deg);
    width: 100%;
    bottom: 50%;
  }
  100% {
    transform: rotate(-135deg);
    width: 100%;
    bottom: 50%;
  }
}
@keyframes menuAnimationBackTopSpan {
  25% {
    transform: rotate(-45deg);
    top: 50%;
  }
  90% {
    transform: rotate(0deg);
    top: 50%;
  }
  100% {
    transform: rotate(0);
    top: 0;
    left: 0;
  }
}
@keyframes menuAnimationBackBottomSpan {
  25% {
    transform: rotate(-135deg);
    width: 100%;
    bottom: 50%;
  }
  90% {
    transform: rotate(0deg);
    width: 100%;
    bottom: 50%;
  }
  100% {
    transform: rotate(0);
    bottom: 0;
    left: 0;
    width: 50px;
  }
}
