.header-menu__button span {
  position: absolute;
  display: block;
  background-color: #8A694F;
  height: 2px;
  transition: 0.5s;
}
.header-menu__button span:nth-child(1) {
  top: 0;
  left: 0;
  width: 100%;
}
.header-menu__button span:nth-child(2) {
  bottom: 0;
  left: 0;
  width: 5rem;
}
.header-menu__button.animate span:nth-child(1) {
  animation-duration: 0.5s;
  animation-name: menuAnimationTopSpan;
}
.header-menu__button.animate span:nth-child(2) {
  animation-duration: 0.5s;
  animation-name: menuAnimationBottomSpan;
}
.header-menu__button.animate-back span:nth-child(1) {
  animation-duration: 0.5s;
  animation-name: menuAnimationBackTopSpan;
}
.header-menu__button.animate-back span:nth-child(2) {
  animation-duration: 0.5s;
  animation-name: menuAnimationBackBottomSpan;
}
.header-menu__button.active span:nth-child(1) {
  transform: rotate(-45deg);
  top: 50%;
